<link href="https://fonts.googleapis.com/css?family=Questrial" rel="stylesheet">
.{
    background: #FAFAFA;
    padding: 2%;
    margin: 5% 5%;
    border-radius: 20px;
    border: 10px solid #305A72;
    font: 16px 'Questrial', Arial, Helvetica, sans-serif;
}
h2, h4{
  margin: 2% 0% 0% 5%;
  font: 1.5em 'Questrial', Arial, Helvetica, sans-serif;
  text-decoration: none;
}

form.container-split{
    width: 45%;
    margin: 5% 2% 0% 2%;
}
form.align-left{
  padding: 5%;
}

.form-style ul{
    padding:2%;
    margin:1% 2%;
    list-style:none;
}
.form-style ul li{
    margin-bottom: 2%;
    margin-top: 1%;
    min-height: 45px;
}
.form-style ul li  .field-style{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 2.5%;
    outline: none;
    background-color: rgb(210, 188, 211, .4);
    border: 1px solid rgb(210, 188, 211, .4);
    border-radius: 20px;

}.form-style ul li  .field-style:focus{
    -webkit-box-shadow: 0 0 5px #B0CFE0;
            box-shadow: 0 0 5px #B0CFE0;
    border:1px solid #B0CFE0;
}
.form-style ul li .field-split{
    width: 48%;
    margin-right: 2%;
    margin-bottom: 4%;
}
@media (max-width: 700px){
  form.container-split{
      width: 88%;
      margin: 5% 2% 0% 2%;
  }
  .form-style ul li .field-split{
      width: 88%;
      margin-right: 2%;
      margin-bottom: 2%;
  }
  form.align-left{
    padding: 5%;
  }
}

.form-style ul li .field-full{
    margin-bottom: 4%;
    width: 98%;
}
.form-style ul li input.align-left{
    float:left;
}
.form-style ul li input.align-right{
    float:right;
}
.form-style ul li input[type="button"],
.form-style ul li input[type="submit"] {
    background-color: #58bc82;
    border: 1px solid;
    border-radius: 20px;
    margin: 1% 40%;
    display: inline-block;
    color: #FFFFFF;
    padding: 10px 25px;
    text-decoration: none;
    font: 18px 'Questrial', Arial, Helvetica, sans-serif;
}
.form-style ul li input[type="button"]:hover,
.form-style ul li input[type="submit"]:hover {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #2D77A2), to(#4e0250));
    background: -webkit-linear-gradient(top, #2D77A2 5%, #4e0250 100%);
    background: -o-linear-gradient(top, #2D77A2 5%, #4e0250 100%);
    background: linear-gradient(to bottom, #2D77A2 5%, #4e0250 100%);
    background-color: #58bc82;
}



